@import '../../utils/variables';
@import '../../utils/mixins';

.subscribe {
  line-height: 1.2;
  letter-spacing: 0.8px;
  margin-bottom: 40px;

  &__title {
    text-align: center;
    margin: 18px 0;
    font-size: 24px;
    font-weight: 4;
    background-image: url('../../images/bgi.jpg');
    background-position: center;
    background-clip: text;
    color: transparent;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 12px;
    
    @include on768 {
      flex-direction: row;
      justify-content: center;
      align-items: end;
    }
  }

  &__input {
    font-size: 14px;
    width: 240px;
    padding: 10px 20px;
    background-color: #fff;
    color: $green;
    border: none;
    border-right: 1px solid $green;
    border-left: 1px solid $green;
    outline: none;
    transition: all 500ms;

    @include on768 {
      width: 300px;
      border: none;
      border-bottom: 1px solid $green;
    }

    &:hover {
      cursor: pointer;
    }
  }

  &__button {
    font-size: 14px;
    width: 240px;
    padding: 10px 20px;
    background-color: #fff;
    color: $green;
    border: 1px solid $green;
    border-bottom-right-radius: 4px;
    border-bottom-left-radius: 4px;
    outline: none;
    transition: all 500ms;
    
    @include on768 {
      padding: 10px 30px;
      width: 300px;
      border-radius: 4px;
      border-bottom-left-radius: 0;
    }

    &:hover {
      cursor: pointer;
      background-color: rgba(0, 128, 0, .3);
      color: #fff;
    }

    &--active {
      background-color: $green;
      color: #fff;
      
      &:hover {
        background-color: $green;
        border: 1px solid $green;
      }
    }
  }

  &__success {
    font-size: 12px;
    color: $green;
    text-align: center;
    width: 220px;
    margin: 0 auto;
  }
}
