@import '../../utils/variables';
@import '../../utils/mixins';

.design {
  position: relative;
  line-height: 1.2;
  letter-spacing: 0.8px;
  margin-bottom: 40px;

  &__title {
    text-align: center;
    margin: 18px 0;
    font-size: 24px;
    font-weight: 4;
    background-image: url('../../images/bgi.jpg');
    background-position: center;
    background-clip: text;
    color: transparent;
  }

  &__img {
    width: 100%;
  }

  &__description {
    margin: 18px auto;
    padding: 18px 0;
    text-align: justify;

    &::before {
      content: '';
      margin-left: 40px;
    }
  }

  &__button {
    display: block;
    font-size: 14px;
    width: 280px;
    margin: 0 auto;
    padding: 10px 20px;
    background-color: #fff;
    color: $green;
    border: 1px solid $green;
    border-radius: 20px;
    outline: none;
    transition: all 500ms;
  
    @include on768 {
      padding: 10px 30px;
      width: 300px;
    }

    &:hover {
      cursor: pointer;
      background-color: $green;
      color: #fff;
    }
  }
}
