@import '../../utils/variables';
@import '../../utils/mixins';

.shop {
  max-width: 1140px;
  position: relative;
  line-height: 1.2;
  letter-spacing: 0.8px;
  margin: 0 auto 40px;

  @include on768 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr min-content min-content min-content 1fr;
    grid-template-areas:
      "img n1"
      "img title"
      "img description"
      "img button"
      "img n2";
    align-items: start;
  }

  &__title {
    text-align: center;
    margin: 18px 0;
    font-size: 24px;
    font-weight: 4;
    background-image: url('../../images/bgi.jpg');
    background-position: center;
    background-clip: text;
    color: transparent;
    grid-area: title;
  }

  &__img {
    width: 100%;
    grid-area: img;
  }

  &__description {
    padding: 18px 0;
    text-align: justify;
    grid-area: description;

    &::before {
      content: '';
      margin-left: 40px;
    }
  }

  &__button {
    display: block;
    font-size: 14px;
    width: 280px;
    height: min-content;
    margin: 0 auto;
    padding: 10px 20px;
    background-color: #fff;
    color: $green;
    border: 1px solid $green;
    border-radius: 20px;
    outline: none;
    transition: all 500ms;
    grid-area: button;
  
    @include on768 {
      padding: 10px 30px;
      width: 300px;
    }

    &:hover {
      cursor: pointer;
      background-color: $green;
      color: #fff;
    }
  }
}
